<template>
	<div class="mvi-editor" v-on="listeners">
		<div class="mvi-editor-menus" :style="{border:(border?'':'none')}" v-if="showMenus" :disabled="disabled">
			<m-editor-item
				v-if="showMenuItem(item)"
				v-for="(item, index) in computedMenuKeys"
				:key="'mvi-editor-menu-' + index"
				:value="item"
				:menu="computedMenus[item]"
				ref="menu"
			></m-editor-item>
		</div>
		<div class="mvi-editor-body">
			<div v-if="codeViewShow" v-text="computedValue" key="code" :contenteditable="!disabled" :style="codeViewStyle"
			:class="codeViewClass" ref="codeView" @input="codeViewInput"></div>
			<div v-else ref="content" @click="changeActive" @input="contentInput" @blur="contentBlur" @focus="contentFocus"
			:class="contentClass" key="content"
			:contenteditable="!disabled" :style="contentStyle" v-html="computedValue" :data-placeholder="placeholder"></div>
		</div>
	</div>
</template>

<script>
import $util from '../../util/util.js';
import editorItem from './edotir-item';
export default {
	name: 'm-editor',
	data() {
		return {
			range: null, //
			contentIsFocus:false,//
			codeViewShow:false,//
			html:'',//html
			text:'',//text
			defaultLayerProps: {//
				fixed: false, //fixed
				placement: 'bottom-start', //
				zIndex: 400 //z-index
			},
			defaultMenus: {//
				undo: true, //
				redo: true, //
				removeFormat:true,//
				selectAll:true,//
				divider:true,//
				tag: [
					//
					{
						label: 'H1',
						value: 'h1'
					},
					{
						label: 'H2',
						value: 'h2'
					},
					{
						label: 'H3',
						value: 'h3'
					},
					{
						label: 'H4',
						value: 'h4'
					},
					{
						label: 'H5',
						value: 'h5'
					},
					{
						label: 'H6',
						value: 'h6'
					},
					{
						label: '',
						value: 'p'
					}
				],
				fontSize: [
					//
					{
						label: '12px',
						value: '0.24rem'
					},
					{
						label: '14px',
						value: '0.28rem'
					},
					{
						label: '16px',
						value: '0.32rem'
					},
					{
						label: '20px',
						value: '0.4rem'
					},
					{
						label: '30px',
						value: '0.6rem'
					},
					{
						label: '40px',
						value: '0.8rem'
					}
				],
				fontFamily: ['PingFang SC', 'Helvetica Neue', 'kaiTi', 'Microsoft YaHei', 'Arial', 'sans-serif'], //
				bold: true, //
				italic: true, //
				underline: true, //
				strikeThrough: true, //
				subscript: true, //
				superscript: true, //
				foreColor: [//
					'#333',
					'#ff0000',
					'#505050',
					'#808080',
					'#bbb',
					'#ddd',
					'#eee',
					'#f7f7f7',
					'#0a9455',
					'#03a8f3',
					'#ec1a0a',
					'#f90',
					'#07C160',
					'#0b73de',
					'#e2e7ea',
					'#c3ebff',
					'#c2fdc4',
					'#ffd6b6',
					'#1eacf3',
					'#f9cec3'
				],
				backColor: [//
					'#333',
					'#ff0000',
					'#505050',
					'#808080',
					'#bbb',
					'#ddd',
					'#eee',
					'#f7f7f7',
					'#0a9455',
					'#03a8f3',
					'#ec1a0a',
					'#f90',
					'#07C160',
					'#0b73de',
					'#e2e7ea',
					'#c3ebff',
					'#c2fdc4',
					'#ffd6b6',
					'#1eacf3',
					'#f9cec3'
				],
				list: [//
					{
						label: '',
						value: 'ol',
						icon:'ol'
					},
					{
						label: '',
						value: 'ul',
						icon:'ul'
					}
				],
				justify: [//
					{
						label: '',
						value: 'left',
						icon: 'align-left'
					},
					{
						label: '',
						value: 'center',
						icon:'align-center'
					},
					{
						label: '',
						value: 'right',
						icon: 'align-right'
					},
					{
						label: '',
						value: 'justify',
						icon: 'align-justify'
					}
				],
				quote: true, //
				link: [//
					{			
						label: '',
						value: 'link'
					}
				],
				image: [//
					{
						label: '',
						value: 'upload'
					},
					{
						label: '',
						value: 'remote'
					}
				],
				video: [//
					{
						label: '',
						value: 'upload'
					},
					{
						label: '',
						value: 'remote'
					}
				],
				table: [//
					{
						label: '',
						value: 'table'
					}
				],
				code: true ,//
				codeView:false,//
			},
			defaultTooltips: {//
				undo: '',
				redo: '',
				removeFormat:'',
				selectAll:'',
				divider:'',
				tag: '',
				fontSize: '',
				fontFamily: '',
				bold: '',
				italic: '',
				underline: '',
				strikeThrough: '',
				subscript: '',
				superscript: '',
				foreColor: '',
				backColor: '',
				list: '',
				justify: '',
				quote: '',
				link: '',
				image: '',
				video: '',
				table: '',
				code: '',
				codeView:''
			},
			defaultTooltipProps: {//
				placement: 'bottom',
				timeout: 400,
				color: '#333',
				textColor: '#fff',
				borderColor: '#333'
			},
			defaultUploadImageProps: {//
				multiple: false, //
				allowedFileType: ['jpg', 'png', 'JPG', 'PNG', 'JPEG', 'jpeg', 'gif', 'GIF'], //
				accept: 'image', //
				minSize: -1, //kb
				maxSize: -1, //kb
				minLength: -1, //
				maxLength: -1 //
			},
			defaultUploadVideoProps: {//
				multiple: false, //
				allowedFileType: ['mp4', 'MP4', 'avi', 'AVI', 'WAV', 'wav'], //
				accept: 'video', //
				minSize: -1, //kb
				maxSize: -1, //kb
				minLength: -1, //
				maxLength: -1 //
			},
			defaultVideoShowProps: {//
				autoplay: true, //
				muted: true, //
				controls: false, //
				loop: false //
			},
			defaultMenuIcons:{//
				undo:'undo',
				redo:'redo',
				removeFormat:'clear',
				selectAll:'check',
				divider:'divider',
				tag:'font-title',
				bold:'bold',
				fontSize:'font-size',
				fontFamily:'font',
				italic:'italic',
				underline:'underline',
				strikeThrough:'strikethrough',
				subscript:'subscript',
				superscript:'superscript',
				foreColor:'color-picker',
				backColor:'brush',
				link:'link',
				list:'ul',
				justify:'align-justify',
				quote:'quote',
				image:'image',
				table:'table-alt',
				video:'video',
				code:'code',
				codeView:'eye'
			}
		};
	},
	props: {
		//
		value: {
			type: String,
			default: ''
		},
		//
		autofocus:{
			type:Boolean,
			default:false
		},
		//
		placeholder: {
			type: String,
			default: ''
		},
		//
		height: {
			type: String,
			default: null
		},
		//
		autoHeight: {
			type: Boolean,
			default: false
		},
		//
		disabled: {
			type: Boolean,
			default: false
		},
		//
		showMenus:{
			type:Boolean,
			default:true
		},
		//
		border:{
			type:Boolean,
			default:true
		},
		//
		menus: {
			type: Object,
			default: function() {
				return {};
			}
		},
		//
		layerProps: {
			type: Object,
			default: function() {
				return {};
			}
		},
		//
		trigger: {
			type: String,
			default: 'click',
			validator(value) {
				return ['hover', 'click'].includes(value);
			}
		},
		//base64
		useBase64: {
			type: Boolean,
			default: true
		},
		//tooltip
		useTooltip:{
			type:Boolean,
			default:true
		},
		//
		tooltips: {
			type: Object,
			default: function() {
				return {};
			}
		},
		//
		tooltipProps: {
			type: Object,
			default: function() {
				return {};
			}
		},
		//
		uploadImageProps: {
			type: Object,
			default: function() {
				return {};
			}
		},
		//
		uploadVideoProps: {
			type: Object,
			default: function() {
				return {};
			}
		},
		//
		uploadImageError: {
			type: Function
		},
		//
		uploadVideoError: {
			type: Function
		},
		//
		videoShowProps: {
			type: Object,
			default: function() {
				return {};
			}
		},
		//
		menuIcons:{
			type:Object,
			default:function(){
				return {}
			}
		}
	},
	computed: {
		listeners() {
			return Object.assign({}, this.$listeners);
		},
		//
		showMenuItem() {
			return key => {
				if (typeof this.computedMenus[key] == 'boolean') {
					return this.computedMenus[key];
				} else if (Array.isArray(this.computedMenus[key])) {
					return this.computedMenus[key].length > 0;
				} else {
					return false;
				}
			};
		},
		//
		computedMenus() {
			var menus = {};
			Object.keys(this.defaultMenus).forEach(key => {
				//
				if (Array.isArray(this.defaultMenus[key])) {
					var newArray = [];
					this.defaultMenus[key].forEach(item => {
						if ($util.isObject(item) && item.label && item.value) {
							var obj = {
								label:item.label,
								value:item.value
							}
							if(item.icon){
								obj.icon = item.icon;
							}
							newArray.push(obj);
						} else if (typeof item == 'string' || $util.isNumber(item)) {
							newArray.push({
								label: item,
								value: item
							});
						}
					});
					menus[key] = newArray;
				} else if (typeof this.defaultMenus[key] == 'boolean') {
					//
					menus[key] = this.defaultMenus[key];
				}
			});
			return menus;
		},
		//
		computedMenuKeys() {
			return Object.keys(this.computedMenus);
		},
		//
		contentClass() {
			var cls = 'mvi-editor-content';
			if (this.autoHeight) {
				cls += ' mvi-editor-content-auto';
			}
			if((this.html == '<p><br></p>' || this.html == '') && !this.contentIsFocus){
				cls += ' mvi-editor-content-empty';
			}
			return cls;
		},
		//
		codeViewClass(){
			var cls = 'mvi-editor-codeview';
			if (this.autoHeight) {
				cls += ' mvi-editor-codeview-auto';
			}
			return cls;
		},
		//
		contentStyle() {
			var style = {};
			if (this.autoHeight) {
				if (this.height) {
					style.minHeight = this.height;
				}
			} else {
				if (this.height) {
					style.height = this.height;
				}
			}
			if(!this.border){
				style.border = 'none';
			}
			return style;
		},
		//
		codeViewStyle(){
			var style = {};
			if (this.autoHeight) {
				if (this.height) {
					style.minHeight = this.height;
				}
			} else {
				if (this.height) {
					style.height = this.height;
				}
			}
			return style;
		},
		//
		computedValue(){
			if(this.value == ''){
				return '<p><br></p>'
			}else {
				return this.value;
			}
		}
	},
	provide() {
		return {
			editor: this
		};
	},
	components: {
		mEditorItem: editorItem
	},
	created() {
		this.initOptions();
	},
	mounted() {
		this.init();
	},
	methods: {
		//
		initOptions(){
			//
			Object.assign(this.defaultLayerProps, this.layerProps);
			//
			Object.assign(this.defaultMenus, this.menus);
			//
			Object.assign(this.defaultTooltips, this.tooltips);
			//
			Object.assign(this.defaultTooltipProps, this.tooltipProps);
			//
			Object.assign(this.defaultUploadImageProps, this.uploadImageProps);
			//
			Object.assign(this.defaultUploadVideoProps, this.uploadVideoProps);
			//
			Object.assign(this.defaultVideoShowProps,this.videoShowProps);
			//
			Object.assign(this.defaultMenuIcons,this.menuIcons);
		},
		//
		init() {
			//
			document.execCommand('defaultParagraphSeparator', false, 'p');
			//css
			document.execCommand('styleWithCSS', false, true);
			//
			this.html = this.$refs.content.innerHTML;
			this.text = this.$refs.content.innerText;
			if(this.autofocus){
				this.collapseToEnd();
				this.$refs.content.focus();
			}
		},
		//api
		insertImage(url) {
			document.execCommand('insertHtml', false, `<img src="${url}" class="mvi-editor-image" />`);
		},
		//api
		insertVideo(url) {
			var video = $util.string2dom(`<video src="${url}" class="mvi-editor-video"></video>`);
			if (this.defaultVideoShowProps.muted) {
				video.setAttribute('muted', 'muted');
			}
			if (this.defaultVideoShowProps.loop) {
				video.setAttribute('loop', 'loop');
			}
			if (this.defaultVideoShowProps.controls) {
				video.setAttribute('controls', 'controls');
			}
			if (this.defaultVideoShowProps.autoplay) {
				video.setAttribute('autoplay', 'autoplay');
			}
			document.execCommand('insertHtml', false, video.outerHTML);
		},
		//api
		empty(){
			this.$refs.content.innerHTML = '<p><br></p>';
			this.html = this.$refs.content.innerHTML;
			this.text = this.$refs.content.innerText;
			this.$emit('change',{
				html:this.html,
				text:this.text
			})
		},
		//
		saveRange() {
			this.range = null;
			var selection = window.getSelection();
			if (selection.getRangeAt && selection.rangeCount) {
				this.range = selection.getRangeAt(0);
			}
		},
		//
		restoreRange() {
			var selection = window.getSelection();
			selection.removeAllRanges();
			if (this.range) {
				selection.addRange(this.range);
			}
		},
		//
		collapseToEnd(){
			var el = null;
			if(this.$refs.content){
				el = this.$refs.content;	
			}else {
				el = this.$refs.codeView;
			}
			el.focus();
			var selection = window.getSelection(); 	
			selection.selectAllChildren(el); 
			selection.collapseToEnd();
		},
		//
		getSelectNode(){
			if(!this.range){
				return null;
			}
			var node = this.range.commonAncestorContainer;
			if($util.isElement(node)){
				return node;
			}else {
				return node.parentNode;
			}
		},
		//
		changeActive(){
			this.saveRange();
			var node = this.getSelectNode();
			this.$refs.menu.forEach((item)=>{
				switch (item.value) {
					case 'bold':
						if($util.getCssStyle(node,'font-weight') == 'bold'
						|| $util.getCssStyle(node,'font-weight') == '700'){
							item.menuActive = true;
						}else{
							item.menuActive = false;
						}
						break;
					case 'italic':
						if($util.getCssStyle(node,'font-style') == 'italic'){
							item.menuActive = true;
						}else{
							item.menuActive = false;
						}
						break;
					case 'underline':
						if($util.getCssStyle(node,'text-decoration-line') == 'underline'){
							item.menuActive = true;
						}else{
							item.menuActive = false;
						}
						break;
					case 'strikeThrough':
						if($util.getCssStyle(node,'text-decoration-line') == 'line-through'){
							item.menuActive = true;
						}else{
							item.menuActive = false;
						}
						break;
					case 'subscript':
						if($util.getCssStyle(node,'vertical-align') == 'sub'){
							item.menuActive = true;
						}else{
							item.menuActive = false;
						}
						break;
					case 'superscript':
						if($util.getCssStyle(node,'vertical-align') == 'super'){
							item.menuActive = true;
						}else{
							item.menuActive = false;
						}
						break;
					case 'link':
						if(node.tagName.toUpperCase() == 'A'){
							item.menuActive = true;
						}else{
							item.menuActive = false;
						}
						break;
					case 'quote':
						if(node.tagName.toUpperCase() == 'BLOCKQUOTE'){
							item.menuActive = true;
						}else{
							item.menuActive = false;
						}
						break;
					case 'table':
						if(node.hasAttribute('mvi-editor-insert-table')){
							item.menuActive = true;
						}else {
							item.menuActive = false;
						}
						break;
					case 'code':
						if(node.tagName.toUpperCase() == 'PRE'){
							item.menuActive = true;
						}else{
							item.menuActive = false;
						}
						break;
				}
			})
		},
		//
		contentInput(){
			if(this.$refs.content.innerHTML == ''){
				this.$refs.content.innerHTML = '<p><br></p>'
			}
			this.changeActive();
			this.html = this.$refs.content.innerHTML;
			this.text = this.$refs.content.innerText;
			this.$emit('change',{
				html:this.html,
				text:this.text
			})
		},
		//
		codeViewInput(){
			if(this.$refs.codeView.innerText == ''){
				this.$refs.codeView.innerText = '<p><br></p>';
				this.collapseToEnd();
			}
			this.html = this.$refs.codeView.innerText;
			var el = $util.string2dom(`<div>${this.$refs.codeView.innerText}</div>`);
			this.text = el.innerText;
			this.$emit('change',{
				html:this.html,
				text:this.text
			})
		},
		//
		contentBlur(){
			this.saveRange();
			this.contentIsFocus = false;
		},
		//
		contentFocus(){
			this.contentIsFocus = true;
		}
	}
};
</script>

<style lang="less">
.mvi-editor-image {
	display: inline-block;
	width: auto;
	height: auto;
	max-width: 100%;
}

.mvi-editor-video {
	display: inline-block;
	width: auto;
	height: auto;
	max-width: 100%;
}
</style>
<style lang="less" scoped>
@import '../../css/mvi-basic.less';

.mvi-editor {
	display: block;
	position: relative;
	width: 100%;
}

.mvi-editor-menus {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	width: 100%;
	flex-wrap: wrap;
	-webkit-flex-wrap: wrap;
	background-color: #fff;
	margin-bottom: @mp-sm;
	border: 1px solid @border-color;
	border-radius: @radius-default;
}

.mvi-editor-body{
	display: block;
	position: relative;
	width: 100%;
	
	.mvi-editor-codeview{
		display: block;
		position: relative;
		width: 100%;
		border: 1px solid #000;
		height: 8rem;
		background-color: #000;
		border-radius: @radius-default;
		margin: 0;
		padding: @mp-sm;
		overflow-x: hidden;
		overflow-y: auto;
		font-size: @font-size-default;
		color: #fff;
		font-family: Consolas;
		
		&.mvi-editor-codeview-auto {
			height: auto;
			min-height: 8rem;
			overflow: hidden;
		}
	}
	
	.mvi-editor-content {
		display: block;
		position: relative;
		width: 100%;
		border: 1px solid @border-color;
		height: 8rem;
		background-color: #fff;
		border-radius: @radius-default;
		margin: 0;
		padding: @mp-sm;
		overflow-x: hidden;
		overflow-y: auto;
		font-size: @font-size-default;
		color: @font-color-default;
	
		&.mvi-editor-content-auto {
			height: auto;
			min-height: 8rem;
			overflow: hidden;
		}
		
		&.mvi-editor-content-empty::before{
			content: attr(data-placeholder);
			font-size: inherit;
			color: inherit;
			opacity: .5;
			line-height: inherit;
			vertical-align: middle;
		}
	}
}
</style>
